.page {

  &__body--with-menu {
    overflow: hidden;

    @include onNotebook {
      overflow: scroll;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }

    @include onNotebook {
      display: none;
    }
  }
}
